// documentation: https://official-stockfish.github.io/docs/stockfish-wiki/UCI-&-Commands.html

const MINIMUM_DEPTH = 1;
const MAXIMUM_DEPTH = 24;

type EngineMessage = {
  /* –∏—Å—Ö–æ–¥–Ω–æ–µ —Å–æ–æ–±—â–µ–Ω–∏–µ –¥–≤–∏–∂–∫–∞ Stockfish */
  uciMessage: string;
  /* –Ω–∞–π–¥–µ–Ω –ª—É—á—à–∏–π —Ö–æ–¥ –¥–ª—è —Ç–µ–∫—É—â–µ–π –ø–æ–∑–∏—Ü–∏–∏: e2e4 */
  bestMove?: string;
  /* —Ö–æ–¥ ¬´ponder¬ª –¥–ª—è –ø—Ä–æ—Ç–∏–≤–Ω–∏–∫–∞: e7e5 */
  ponder?: string;
  /* –æ—Ü–µ–Ω–∫–∞ –ø–æ–∑–∏—Ü–∏–∏ (–≤ –ø–µ—à–µ—á–Ω—ã—Ö –µ–¥–∏–Ω–∏—Ü–∞—Ö, ¬±cp/100) */
  positionEvaluation?: number;
  /* –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –ø–æ–ª—É—Ö–æ–¥–æ–≤ –¥–æ –º–∞—Ç–∞, –µ—Å–ª–∏ –Ω–∞–π–¥–µ–Ω –º–∞—Ç */
  possibleMate?: number;
  /* —Å—Ç—Ä–æ–∫–∞ —Å PV (–ø–æ—Å–ª–µ–¥–æ–≤–∞—Ç–µ–ª—å–Ω–æ—Å—Ç—å —Ö–æ–¥–æ–≤), —Ä–∞–∑–¥–µ–ª—ë–Ω–Ω—ã–º–∏ –ø—Ä–æ–±–µ–ª–∞–º–∏ */
  pv?: string[];
  /* —Ç–µ–∫—É—â–∞—è –≥–ª—É–±–∏–Ω–∞ –∞–Ω–∞–ª–∏–∑–∞ */
  depth?: number;
};

export type EngineMessageCallback = (msg: EngineMessage) => void;

export default class Engine {
  private stockfish: Worker;
  private isInitialized = false;
  private readyPromise: Promise<void>;
  private resolveReady!: () => void;
  private messageCallbacks = new Set<EngineMessageCallback>();
  private _depth: number;
  private _thinkTime: number;

  constructor() {
    this.stockfish = new Worker("./stockfish.js");
    // —Å–æ–∑–¥–∞—ë–º promise, –∫–æ—Ç–æ—Ä—ã–π —Ä–∞–∑—Ä–µ—à–∏—Ç—Å—è –ø–æ—Å–ª–µ –ø–æ–ª—É—á–µ–Ω–∏—è –ø–µ—Ä–≤–æ–≥–æ 'readyok'
    this.readyPromise = new Promise((resolve) => {
      this.resolveReady = resolve;
    });

    this.initializeEngine();
  }

  /**
   * –ü–µ—Ä–≤—ã–π handshake —Å –¥–≤–∏–∂–∫–æ–º:
   * 1) –û—Ç–ø—Ä–∞–≤–ª—è–µ–º 'uci'
   * 2) –ñ–¥—ë–º 'readyok'
   *
   * –ü–æ—Å–ª–µ –ø–æ–ª—É—á–µ–Ω–∏—è –ø–µ—Ä–≤–æ–≥–æ 'readyok' —Ñ–ª–∞–≥ this.isInitialized —Å—Ç–∞–Ω–µ—Ç true,
   * –∏ resolveReady() —Ä–∞–∑—Ä–µ—à–∏—Ç –≤—Å–µ –º–µ—Ç–æ–¥—ã, –æ–∂–∏–¥–∞—é—â–∏–µ –≥–æ—Ç–æ–≤–Ω–æ—Å—Ç–∏ –¥–≤–∏–∂–∫–∞.
   */
  private initializeEngine() {
    this.stockfish.addEventListener("message", this.handleEngineMessage, {
      once: false,
    });

    // –ó–∞–ø—É—Å–∫–∞–µ–º UCI-—Ä–µ–∂–∏–º
    this.stockfish.postMessage("uci");
    // –°–ø—Ä–∞—à–∏–≤–∞–µ–º, –≥–æ—Ç–æ–≤ –ª–∏ –¥–≤–∏–∂–æ–∫
    this.stockfish.postMessage("isready");
  }

  private handleEngineMessage = (e: MessageEvent) => {
    const data: string = e.data;
    // –õ–æ–≥–∏—Ä—É–µ–º raw-—Å–æ–æ–±—â–µ–Ω–∏—è (–º–æ–∂–Ω–æ –æ—Ç–∫–ª—é—á–∏—Ç—å –≤ production)
    // console.log("[Engine ‚Üí]", data);

    // –†–∞—Å–ø–∞—Ä—ã–≤–∞–µ–º —á–∞—Å—Ç—å UCI-–∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏
    const msg = this.parseMessage(data);
    // –û–ø–æ–≤–µ—â–∞–µ–º –≤—Å–µ—Ö –≤–Ω–µ—à–Ω–∏—Ö —Å–ª—É—à–∞—Ç–µ–ª–µ–π
    this.messageCallbacks.forEach((cb) => cb(msg));

    // –ü–µ—Ä–≤—ã–π readyok –ø–æ—Å–ª–µ 'isready'
    if (data === "readyok" && !this.isInitialized) {
      this.isInitialized = true;
      // –†–∞–∑—Ä–µ—à–∞–µ–º —Ç–æ–∑–∏, –∫—Ç–æ –∂–¥—ë—Ç initial ready
      this.resolveReady();
      console.log("ENGINE IS INITIALIZED AND READY!");
    }
  };

  /**
   * –ñ–¥—ë–º, –ø–æ–∫–∞ –¥–≤–∏–∂–æ–∫ –Ω–µ –æ—Ç–≤–µ—Ç–∏—Ç –ø–µ—Ä–≤—ã–º 'readyok'.
   * –í—Å–µ –≤—ã–∑–æ–≤—ã setOption‚Ä¶() –±—É–¥—É—Ç –∂–¥–∞—Ç—å –∏–º–µ–Ω–Ω–æ —ç—Ç—É promise.
   */
  private async waitForInitialReady(): Promise<void> {
    if (!this.isInitialized) {
      await this.readyPromise;
    }
  }

  /**
   * –ü–∞—Ä—Å–∏–Ω–≥ –ª—é–±–æ–≥–æ UCI-—Å–æ–æ–±—â–µ–Ω–∏—è –∏–∑ –¥–≤–∏–∂–∫–∞ –≤ –Ω–∞—à —Ñ–æ—Ä–º–∞—Ç EngineMessage.
   */
  private parseMessage(data: string): EngineMessage {
    const bestMove = data.match(/bestmove\s+(\S+)/)?.[1];
    const ponder = data.match(/ponder\s+(\S+)/)?.[1];
    const cpMatch = data.match(/score cp\s+(-?\d+)/);
    const mateMatch = data.match(/score mate\s+(-?\d+)/);
    const depthMatch = data.match(/info.*\bdepth\s+(\d+)/);
    const pvMatch = data.match(/info.*\b pv\s+(.+)/);

    return {
      uciMessage: data,
      bestMove: bestMove || undefined,
      ponder: ponder || undefined,
      positionEvaluation: cpMatch ? parseInt(cpMatch[1], 10) / 100 : undefined,
      possibleMate: mateMatch ? parseInt(mateMatch[1], 10) : undefined,
      pv: pvMatch ? pvMatch[1].trim().split(/\s+/) : undefined,
      depth: depthMatch ? Number(depthMatch[1]) : undefined,
    };
  }

  /**
   * –î–æ–±–∞–≤–ª—è–µ–º –≤–Ω–µ—à–Ω–µ–≥–æ —Å–ª—É—à–∞—Ç–µ–ª—è ‚Äî –æ–Ω –±—É–¥–µ—Ç –ø–æ–ª—É—á–∞—Ç—å –∫–∞–∂–¥—ã–π EngineMessage
   */
  addMessageListener(callback: EngineMessageCallback) {
    this.messageCallbacks.add(callback);
  }

  removeMessageListener(callback: EngineMessageCallback) {
    this.messageCallbacks.delete(callback);
  }

  /**
   * –û—Ç–ø—Ä–∞–≤–∫–∞ –∫–æ–º–∞–Ω–¥—ã ¬´–æ—Å—Ç–∞–Ω–æ–≤–∏—Ç—å—Å—è¬ª (–ø—Ä–µ—Ä—ã–≤–∞–µ—Ç —Ç–µ–∫—É—â–∏–π –ø–æ–∏—Å–∫).
   */
  stop() {
    this.stockfish.postMessage("stop");
  }

  /**
   * –ü–æ–ª–Ω–æ–µ –∑–∞–≤–µ—Ä—à–µ–Ω–∏–µ —Ä–∞–±–æ—Ç—ã —Å –¥–≤–∏–∂–∫–æ–º:
   * - –û—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –≤—Å–µ –≤—ã—á–∏—Å–ª–µ–Ω–∏—è
   * - –£–¥–∞–ª—è–µ–º –≤—Å–µ—Ö —Å–ª—É—à–∞—Ç–µ–ª–µ–π
   * - –û—Ç–ø—Ä–∞–≤–ª—è–µ–º 'quit'
   */
  terminate() {
    this.stop();
    this.stockfish.postMessage("quit");
    this.messageCallbacks.clear();
  }

  /****************************
   *   –ú–µ—Ç–æ–¥—ã —É—Å—Ç–∞–Ω–æ–≤–∫–∏ –æ–ø—Ü–∏–π  *
   ****************************/

  /**
   * –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ—Ç —É—Ä–æ–≤–µ–Ω—å –Ω–∞–≤—ã–∫–∞ (Skill Level).
   * –û—Ç–ª–æ–∂–∏—Ç –∫–æ–º–∞–Ω–¥—É, –ø–æ–∫–∞ –¥–≤–∏–∂–æ–∫ –Ω–µ —Å—Ç–∞–Ω–µ—Ç ready –≤–ø–µ—Ä–≤—ã–µ.
   */
  setSkillLevel(level: number) {
    this.waitForInitialReady().then(() => {
      this.stockfish.postMessage(`setoption name Skill Level value ${level}`);
    });
  }

  /**
   * –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ—Ç –º–∞–∫—Å–∏–º–∞–ª—å–Ω—É—é –≥–ª—É–±–∏–Ω—É –ø–æ–∏—Å–∫–∞ (UCI_option "Depth").
   */
  setDepth(depth: number) {
    if (depth < MINIMUM_DEPTH) depth = MINIMUM_DEPTH;
    else if (depth > MAXIMUM_DEPTH) depth = MAXIMUM_DEPTH;
    this._depth = depth;

    this.waitForInitialReady().then(() => {
      this.stockfish.postMessage(`setoption name Depth value ${depth}`);
    });
    console.log(`–ù–æ–≤–∞—è –≥–ª—É–±–∏–Ω–∞ –ø–æ–∏—Å–∫–∞: ${this._depth}`);
  }

  /**
   * –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ—Ç ¬´think time¬ª ‚Äî –æ–≥—Ä–∞–Ω–∏—á–µ–Ω–∏–µ –ø–æ –≤—Ä–µ–º–µ–Ω–∏ (–≤ –º–∏–ª–ª–∏—Å–µ–∫—É–Ω–¥–∞—Ö).
   * –°–∞–º–æ –ø–æ —Å–µ–±–µ –∑–Ω–∞—á–µ–Ω–∏–µ –Ω–µ –ø–µ—Ä–µ–¥–∞—ë—Ç—Å—è –≤ setoption:
   * –º—ã –±—É–¥–µ–º –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å –µ–≥–æ –≤ goCommand (–∏–ª–∏ –º–æ–∂–Ω–æ —Å–Ω—è—Ç—å –æ–≥—Ä–∞–Ω–∏—á–µ–Ω–∏–µ).
   */
  setThinkTime(time: number) {
    this._thinkTime = time;
    console.log(`–ù–æ–≤–æ–µ –≤—Ä–µ–º—è –Ω–∞ —Ö–æ–¥ (–º–∏–ª–ª–∏—Å–µ–∫—É–Ω–¥—ã): ${this._thinkTime}`);
  }

  /**
   * –ü–æ—Ç–æ–∫–∏, –∫–æ—Ç–æ—Ä—ã–µ –¥–≤–∏–∂–æ–∫ –º–æ–∂–µ—Ç –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å (UCI_option "Threads").
   *
   */
  setThreads(threads: number) {
    this.waitForInitialReady().then(() => {
      this.stockfish.postMessage(`setoption name Threads value ${threads}`);
    });
  }

  /**
   * –°–∫–æ–ª—å–∫–æ –≤–∞—Ä–∏–∞–Ω—Ç–æ–≤ (MultiPV) –≤–æ–∑–≤—Ä–∞—â–∞—Ç—å.
   */
  setMultiPV(n: number) {
    this.waitForInitialReady().then(() => {
      this.stockfish.postMessage(`setoption name MultiPV value ${n}`);
    });
  }

  /**
   * –í–∫–ª—é—á–∞–µ—Ç –∏–ª–∏ –æ—Ç–∫–ª—é—á–∞–µ—Ç –æ–≥—Ä–∞–Ω–∏—á–µ–Ω–∏–µ –ø–æ —Ä–µ–π—Ç–∏–Ω–≥—É, –∏ –∑–∞–¥–∞—ë—Ç Elo.
   * (UCI_option UCI_LimitStrength –∏ UCI_Elo).
   */
  setLimitStrength(limit: boolean, elo = 1320) {
    this.waitForInitialReady().then(() => {
      this.stockfish.postMessage(
        `setoption name UCI_LimitStrength value ${limit ? "true" : "false"}`
      );
      // –ï—Å–ª–∏ limit=false, –∑–Ω–∞—á–µ–Ω–∏–µ UCI_Elo –≤—Å—ë —Ä–∞–≤–Ω–æ ¬´–ø—Ä–∏–º–µ—Ç—Å—è¬ª, –Ω–æ –Ω–µ –±—É–¥–µ—Ç –≤–ª–∏—è—Ç—å.
      this.stockfish.postMessage(`setoption name UCI_Elo value ${elo}`);
    });
  }

  /*********************************************
   *    –û—Å–Ω–æ–≤–Ω–æ–π –º–µ—Ç–æ–¥: –∞–Ω–∞–ª–∏–∑ –ø–æ–∑–∏—Ü–∏–∏ (go)     *
   *********************************************/

  /**
   * –ó–∞–ø—Ä–∞—à–∏–≤–∞–µ—Ç –æ—Ç –¥–≤–∏–∂–∫–∞ –ª—É—á—à–∏–π —Ö–æ–¥ –¥–ª—è –ø–æ–∑–∏—Ü–∏–∏ FEN.
   * –¢–µ–ø–µ—Ä—å –∫–∞–∂–¥—ã–π —Ä–∞–∑ –ø–µ—Ä–µ–¥ –ø–æ–∏—Å–∫–æ–º –º—ã:
   *  1) –û—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –ø—Ä–µ–¥—ã–¥—É—â–∏–π –ø–æ–∏—Å–∫ (stop)
   *  2) –ü–æ—Å—ã–ª–∞–µ–º 'ucinewgame', 'isready' ‚Üí –∂–¥—ë–º –Ω–æ–≤–æ–≥–æ 'readyok'
   *  3) –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –ø–æ–∑–∏—Ü–∏—é ‚Üí ¬´go depth N¬ª
   */
  async evaluatePosition(fen: string) {
    // –ñ–¥—ë–º, –ø–æ–∫–∞ –¥–≤–∏–∂–æ–∫ –≤–æ–æ–±—â–µ –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä–æ–≤–∞–Ω
    await this.waitForInitialReady();

    // 1) –ü—Ä–µ—Ä—ã–≤–∞–µ–º –ø—Ä–µ–¥—ã–¥—É—â–∏–π –∞–Ω–∞–ª–∏–∑ (–µ—Å–ª–∏ –æ–Ω –±—ã–ª)
    this.stop();

    // 2) –°–±—Ä–∞—Å—ã–≤–∞–µ–º –≤–Ω—É—Ç—Ä–µ–Ω–Ω–µ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ –¥–≤–∏–∂–∫–∞ –ø–µ—Ä–µ–¥ –Ω–æ–≤—ã–º –∞–Ω–∞–ª–∏–∑–æ–º
    await new Promise<void>((resolve) => {
      const onReadyForNewGame = (e: MessageEvent) => {
        if (e.data === "readyok") {
          this.stockfish.removeEventListener("message", onReadyForNewGame);
          resolve();
        }
      };
      this.stockfish.addEventListener("message", onReadyForNewGame);
      this.stockfish.postMessage("ucinewgame");
      this.stockfish.postMessage("isready");
    });

    // 3) –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –ø–æ–∑–∏—Ü–∏—é
    this.stockfish.postMessage(`position fen ${fen}`);

    // 4) –§–æ—Ä–º–∏—Ä—É–µ–º –∫–æ–º–∞–Ω–¥—É ¬´go¬ª
    let goCommand = `go`;
    if (this._depth) goCommand += ` depth ${this._depth}`;
    if (this._thinkTime) goCommand += ` movetime ${this._thinkTime}`;
    console.log(`[Engine ‚Üê] ${goCommand}`);
    this.stockfish.postMessage(goCommand);
  }
}
